@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Times New Roman', Times, serif;
}

.home, .alerts, .reports{
  display: -webkit-flex;
  display: flex;
  height: 90vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 3rem;
  background-color: #F7F7F7 ;
}
.navbar{
    background-color: white  ;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    border-width: 10px;
    border-color: black;
}

.menu-bars{
    margin-left: 2rem;
    font-size: 1.5rem;
    background: white;
}

.nav-menu{
    background-color: white ;
    width: 250px;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: fixed;
    top: 0;
    transition: 850ms;
    left: -100%;
}

.nav-menu.active{
    left: 0;
    transition: 350ms;
}

.nav-text{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    padding: 20px 10px 8px 16px;
}

.nav-text a {
    text-decoration: none;
    color: black;
    font-size: 22px;
    width: 95%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: right;
            align-items: right;
    padding: 20px 16px;
    border-radius: 10px;

}

.nav-text a:hover{
    background-color: #E8EEF8 ;
}

.nav-menu-items{
    width: 100%;

}

.navbar-toggle{
    background-color: white;
    width: 100%;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
}

.span{
    margin-left: 16px;
}

.h1{
    margin-top: -280px;
    margin-left: 80px;
    font-size: 36px;
    text-align: justify;
}

.subHeading{
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 24px;
    margin-top: -40px;
    margin-left: -80px;
    background-color: #E8EEF8;
    border: solid #E8EEF8;
    font-weight: lighter;
    width: 122%;
}


.content {
    margin: auto;
    margin-top: 5%;
    width: 100%;
    height: auto;
    background-color: #F7F7F7;
    padding: 20px;
    border-radius: 8px;
  }
  
  .title {
    padding-top: 100px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 4em;
    letter-spacing: 0.15rem;
    color: #F7F7F7;
  }
  
  label {
    color: #333;
  }
  
  .result-container {
    text-align: center;
    font-weight: 600;
    font-size: 4em;
    letter-spacing: 0.15rem;
    padding: 20px 0 0;
  }
  
  #result {
    color: #000;
  }
